import { createRouter, createWebHistory } from 'vue-router'
import HomeView from '../views/HomeView.vue'
import CommercialView from '../views/CommercialView.vue'

const routes = [
  {
    path: '/',
    name: '',
    component: HomeView
  },
  {
    path: '/commercial-works',
    name: ' / Commerical Works',
    component: CommercialView
  },
  {
    path: '/rugs',
    name: ' / Rugs',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () =>
      import(/* webpackChunkName: "about" */ '../views/RugsView.vue')
  },
  {
    path: '/pictures',
    name: ' / Pictures',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () =>
      import(/* webpackChunkName: "about" */ '../views/PicturesView.vue')
  },
  {
    path: '/typography',
    name: ' / Typography',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () =>
      import(/* webpackChunkName: "about" */ '../views/TypographyView.vue')
  },
  {
    path: '/left-field',
    name: ' / Left-Field',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () =>
      import(/* webpackChunkName: "about" */ '../views/LeftView.vue')
  }
]

const router = createRouter({
  history: createWebHistory(),
  routes
})

export default router
