        <a
          v-if="link.page_name.toLowerCase() === 'playlist'"
          target="_blank"
          :href="spotifyLink.url"
          class="col-12 mb-1 col-md-auto"
          >PLAYLIST</a
        >
        <span
          :class="[link.visible ? 'active-link' : 'not-active-link']"
          v-else
          @click="navigateTo(link)"
        >
          {{ link.page_name }}
        </span>
      </div>

