<script>
export default {
  data() {
    return {
      navigationButtons: [],
      spotifyLink: [],
    };
  },
  methods: {
    getContent() {
      this.$prismic.client.getSingle("about").then((response) => {
        this.navigationButtons = response.data.navigation_buttons;
        this.spotifyLink = response.data.spotify;
      });
    },
    navigateTo(link) {
      if (link.visible) {
        this.$router.push(link.page_name.replace(/\s+/g, "-").toLowerCase());
      }
    },
  },
  created() {
    this.getContent();
  },
};
</script>

